    [
        "平等，建立在对岗位的誓死捍卫基础之上，即：对岗位职责负责，对岗位价值输出负责，不给团队拖后腿，争当火车头；人有岗位不同，但无高低之分，对于践行自己的岗位职责和价值输出，人人平等，对于践行公司文化，践行成长评估，人人平等。",
