import axios from "axios";
// 引入全局配置
import { config } from "@/config/config";

// 登录验证
const loginApi = (loginForm) => new Promise((resolve, reject) => {
  let data = {
    username: loginForm.username,
    password: loginForm.password,
  };
  axios({
    method: "post",
    url: "/api/login",
    headers: {
      "Content-Type": "application/json",
    },
    data: JSON.stringify(data),
  })
   .then((response) => {
      resolve(response);
    })
   .catch((error) => {
      reject(error.response);
    });
});

// 登出
const logoutApi = () => new Promise((resolve, reject) => {
  axios({
    method: "get",
    url: "/api/logout",
    headers: {
      "Content-Type": "application/json",
    },
  })
   .then((response) => {
      // 登出后清除 sessionId 和相关存储
      localStorage.removeItem(config.sessionId);
      localStorage.removeItem(config.userInfo);
      localStorage.removeItem(config.userLoginTimestamp);
      resolve(response);
    })
   .catch((error) => {
      reject(error.response);
    });
});

// 检查session是否存在
const checkSessionApi = () => new Promise((resolve, reject) => {
  axios({
    method: "get",
    url: "/api/checkSession",
    headers: {
      "Content-Type": "application/json",
    },
  })
   .then((response) => {
      resolve(response);
    })
   .catch((error) => {
      reject(error.response);
    });
});

export { loginApi, logoutApi, checkSessionApi };