import axios from "axios";

//查看评估节点
const getStepTimeApi = () => new Promise((resolve, reject) =>{
    axios({
        method:'get',
        url:'/api/find/step',
        headers:{
            "Content-Type": "application/json",
        },
    }).then((response)=>{
        resolve(response)
    }).catch((error)=>{
        reject(error.response)
    })
})

// 修改/发布当前时间
const getEditTimeApi = (isNew, newtime, cycleName) => new Promise((resolve, reject) =>{
    let data = {
        isNew: isNew,
        first: {
            description:'建立评估矩阵关系',
            startDate:newtime[0],
            endDate:newtime[1]
        },
        second: {
            description:'上级评估/周边评估',
            startDate:newtime[2],
            endDate:newtime[3]
        },
        third: {
            description:'结果沟通',
            startDate:newtime[4],
            endDate:newtime[5]
        },
        fourth: {
            description:'结果发布',
            startDate:newtime[6],
            endDate:newtime[7]
        },
        title: cycleName
      };
    axios({
        method:'post',
        url:'/api/edit/step',
        headers:{
            "Content-Type": "application/json",
        },
        data: JSON.stringify(data),
    }).then((response)=>{
        resolve(response)
    }).catch((error)=>{
        reject(error.response)
    })
})

export{
    getStepTimeApi,
    getEditTimeApi
}