<template>
    <!-- 使用a-layout布局组件 -->
    <a-layout>
        <!-- 顶部导航区域，设置为窗口高度的10% -->
        <a-layout-header class="header">
            <!-- Logo区域 -->
            <div class="sysLogo">
                <img src="@/assets/sysLogo.png" alt="logo" class="logo-img"/>
                <img src="@/assets/slogan.png" alt="slogan" class="slogan">
            </div>
            <!-- 头像名字区域 -->
            <div class="userName">
                <a-avatar size="large" :style="{ backgroundColor: '#1E90FF', verticalAlign: 'middle', marginRight: '10px' }" :gap="gap">User</a-avatar>
                <a-dropdown placement='bottomRight'>
                    <span>您好！{{ username }}</span>
                    <template #overlay>
                        <a-menu>
                            <a-menu-item @click="logout">
                                退出登录
                            </a-menu-item>
                        </a-menu>
                    </template>
                </a-dropdown>
            </div>
        </a-layout-header>
        <!-- 主布局，包含侧边栏和内容区，主布局高度设置为90vh -->
        <a-layout :style="{ height: '90vh', padding: '15px 0 0 0' }">
            <!-- 侧边栏布局，宽度为200px，背景色为白色 -->
            <a-layout-sider width="200" style="background: #fff;">
                <!-- 侧边栏菜单，selectedKeys2用于控制选中项，openKeys控制哪些子菜单展开 -->
                <a-menu v-model:selectedKeys="selectedKeys" @click="handleMenuClick"
                    :style="{ height: '100%', borderRight: 0 }">
                    <a-menu-item key="个人得分">
                        <CheckCircleOutlined />
                        <span>个人得分</span>
                    </a-menu-item>
                    <a-menu-item key="我的待办">
                        <CommentOutlined />
                        <span>我的待办</span>
                    </a-menu-item>
                    <a-menu-item key="修改个人信息">
                        <UserOutlined />
                        <span>修改个人信息</span>
                    </a-menu-item>
                    <a-menu-item key="我的操作日志">
                        <TeamOutlined />
                        <span>我的操作日志</span>
                    </a-menu-item>
                </a-menu>
            </a-layout-sider>

            <!-- 主内容布局，内容区添加了圆角和阴影 -->
            <a-layout style="padding: 10px 20px 20px 20px">
                <a-layout-content :style="{
                    background: '#fff',
                    padding: '10px',
                    margin: 0,
                    minHeight: '280px',
                    borderRadius: '10px',
                    overflow: 'auto',
                    boxShadow: '0px 4px 12px rgba(0, 0, 0, 0.1)'
                }">
                    <router-view></router-view>
                </a-layout-content>
            </a-layout>
        </a-layout>
    </a-layout>
</template>

<script setup>
import { CommentOutlined, CheckCircleOutlined, TeamOutlined, UserOutlined } from '@ant-design/icons-vue';
import { ref, onMounted } from 'vue'; // 从Vue中导入ref用于定义响应式变量
import { useRouter, useRoute } from 'vue-router';
import { logoutApi } from '@/api/login'
import { message } from 'ant-design-vue';

onMounted(() => {
    // 页面加载时，根据当前路由路径设置 selectedKeys
    switch (route.path) {
        case '/todoList':
            selectedKeys.value = ['我的待办'];
            break;
        case '/personalScore':
        case '/personalMark':
            selectedKeys.value = ['个人得分'];
            break;
        case '/actionLog':
            selectedKeys.value = ['我的操作日志'];
            break;
        case '/personalInfo':
            selectedKeys.value = ['修改个人信息'];
            break;
        default:
            selectedKeys.value = ['个人得分'];
            break;
    }
});

const router = useRouter();
const route = useRoute(); // 用于获取当前路由信息
const selectedKeys = ref();
const username = ref(localStorage.getItem('username'));

// 点击菜单项时，实现页面跳转
const handleMenuClick = (event) => {
    if (event.key === '我的待办') {
        router.push({ path: '/todoList' })
    } else if (event.key === '个人得分') {
        router.push({ path: '/personalScore' })
    } else if (event.key === '我的操作日志') {
        router.push({ path: '/actionLog' })
    } else if (event.key === '修改个人信息') {
        router.push({ path: '/personalInfo' })
    } else {
        router.push({ path: '/personalScore' })
    }
};

// 退出登录（后面调用登出api）
const logout = () => {
    logoutApi().then(res => {
        if(res.data.code === 200){
            router.push({ path: '/' })
            message.success('您已退出登录！');
        }
    }).catch(err => {
        if(err.data.code === 403){
            router.push({ path: '/' })
            message.error('登录信息已失效，请重新登录！');
        }else {
            console.log('退出登录失败！', err)
        }
    })
}

</script>

