        display: flex; /* 使用 Flexbox 布局 */
        align-items: center; /* 垂直居中对齐 */
        justify-content: flex-start; /* 水平排列，保持 logo 在左边 */

        .logo-img {
            width: 60px; /* 设置固定宽度 */
            height: 60px; /* 设置固定高度 */
            object-fit: contain; /* 保持图片比例 */
