<template>
    <div class="main">
        <div class="overlay"></div>
        <div class="logincard">
            <div class="left">
                <div class="welcomeTitle">
                    <span>欢迎使用</span><br>
                    <span style="font-size: 28px; margin-top: 30px;display: block;">腾微公司成长评估系统</span>
                </div>
                <div class="rightPic">
                    <img src="@/assets/loginPic.png" alt="">
                </div>
            </div>
            <div class="right">
                <span class="nameTitle">成长评估系统</span>
                <span class="loginText">登录</span>
                <div class="loginForm"> 
                    <a-form :model="loginForm" ref="formRef">
                        <a-form-item name="username" :rules="[{ required: true, message: '请输入账号！' }]">
                            <a-input v-model:value="loginForm.username" placeholder="请输入账号" class="custom-input" allowClear>
                                <template #prefix>
                                    <UserOutlined style="color: rgba(0, 0, 0, 0.25)" />
                                </template>
                            </a-input>
                        </a-form-item>
                        <a-form-item name="password" :rules="[{ required: true, message: '请输入密码!' }]">
                            <a-input-password v-model:value="loginForm.password" type="password" placeholder="请输入密码"
                            class="custom-input" allowClear>
                            <template #prefix>
                                    <LockOutlined style="color: rgba(0, 0, 0, 0.25)" />
                            </template>
                            </a-input-password>
                        </a-form-item>
                    </a-form>
                </div>
                <div class="loginBtn">
                    <a-button style="width: 80px;" type="primary" @click="handleSubmit">
                    登录
                </a-button>
                </div>
            </div>
        </div>
    </div>
</template>

<script setup>
import { onMounted, reactive, ref } from 'vue';
import { UserOutlined, LockOutlined } from '@ant-design/icons-vue';
import { useRouter } from 'vue-router'
import { message } from 'ant-design-vue';
import { loginApi, checkSessionApi } from '@/api/login'
import { config } from '@/config/config'
import { useStore } from 'vuex';  // 引入 vuex store
import { getStepTimeApi } from "@/api/scoreTimeSet"

const store = useStore();
const router = useRouter()
const loginForm = reactive({
    username: '',
    password: '',
});

const formRef = ref(null) //创建对表单的引用
// 登录逻辑
const handleSubmit = async (e) => {
    e.preventDefault();

    try {
        // 验证表单
        await formRef.value.validate();

        // 调用后端登录接口
        const res = await loginApi(loginForm);
        if (res.data.code === 200) {
            const userData = {
                code: res.data.code,
                username: res.data.login_message.username,
                msg: res.data.msg,
                role: res.data.role,
                login_message: res.data.login_message,
                isFirstLogin: res.data.isFirstLogin
            }
            handleLoginSuccess(userData);
        } else {
            handleLoginFailure(res.data.msg);
        }
    } catch (error) {
        // 若登录接口调用失败（如果error.response存在，说明问题发生在调用后端接口时）
        if (error.response) {
            message.error("登录接口调用失败！");
        }
        // 验证失败
        else {
            message.error("登录失败！请检查账号或密码输入~");
        }
    }
};

// 登录成功处理
const handleLoginSuccess = (userData) => {
    message.success({
        content: '登录成功！祝您度过愉快的一天~',
        style: { marginTop: '5vh' },
        duration: 2,  // 设置显示时间为 2 秒
    });

    // 获取当前评估环节
    getStepTime();

    // 存储用户信息
    localStorage.setItem('username', userData.username);
    localStorage.setItem('userRole', userData.role + '');
    localStorage.setItem('isFirstLogin', userData.isFirstLogin);

    // 跳转到主页
    setTimeout(() => {
        router.replace({ path: '/homePage' });
    }, 100);
};

// 登录失败处理
const handleLoginFailure = (errorMessage, redirectToLogin = false) => {
    message.error(errorMessage);
    if (redirectToLogin) {
        router.replace({ path: '/' });  //跳转到登录页面
    }
};

// 登录时获得当前评估进度
const getStepTime = () => {
    getStepTimeApi()
        .then(res => {
            if (res.data.code === 200) {
                for (let i = 0; i <= res.data.data.evaluateStep.length - 1; i++) {
                    if (res.data.data.evaluateStep[i].enable === 1) {
                        localStorage.setItem('currentSession', i + 1);
                    }
                }
            } else {
                message.error('获取当前评估进度失败！'+res.data.msg);
            }
        })
        .catch(err => {
            console.log('获取失败！', err);
        });
};

// 自动登录逻辑：检查 Session 是否存在
const checkSession = async () => {
    try {
        // 检查是否有有效的 Session
        const res = await checkSessionApi();  // 假设有一个后端接口检查 Session 是否有效

        if (res.data.code === 200) {
            // 如果有有效的 Session，获取用户数据并跳转
            const userData = {
                username: res.data.login_message.username,
                msg: res.data.msg,
                role: res.data.role,
                login_message: res.data.login_message,
                isFirstLogin: res.data.isFirstLogin
            }
            handleLoginSuccess(userData);
        } else {
            // 如果没有有效 Session，不自动执行任何逻辑，直接结束函数
            return;            
        }
    } catch (error) {
        console.log('检查登录状态接口调用失败');
    }
};

// 在页面加载时执行 checkSession 函数来检查是否已经登录
onMounted(() => {
    checkSession();
});
</script>

