// router/index.js
import { createRouter, createWebHistory } from 'vue-router'
import Login from '@/page/Login.vue'
import HomePage from '@/page/HomePage.vue'
import OrdinaryLayout from '@/components/layout/OrdinaryLayout.vue'
import AdminLayout from '@/components/layout/AdminLayout.vue'
import { message } from 'ant-design-vue'

const routes = [
    // 登录页
    {
        path: '/',
        name: 'login',
        component: Login,
        meta: { requiresAuth: false } // 登录页不需要验证
    },
    // 系统首页
    {
        path: '/homePage',
        name: 'homePage',
        component: HomePage,
        meta: { requiresAuth: false } // 系统首页不需要验证
    },
    // 普通用户端
    {
        path: '/ordinary',
        name: 'ordinary',
        component: OrdinaryLayout,
        redirect: '/personalScore',
        meta: { requiresAuth: true, role: ['0'] }, // 普通用户的角色是 '0'
        children: [
            // 个人得分
            {
                path: '/personalScore',
                name: 'personalScore',
                component: () => import('@/page/ordinary/PersonalScore'),
                meta: {title: '普通用户-个人得分'},
            },
            // 我的待办
            {
                path: '/todoList',
                name: 'todoList',
                component: () => import('@/page/ordinary/TodoList'),
                meta: {title: '普通用户-我的待办'},
            },
            // 修改个人信息
            {
                path: '/personalInfo',
                name: 'personalInfo',
                component: () => import('@/page/ordinary/PersonalInfo'),
                meta: {title: '普通用户-修改个人信息'},
            },
            // 我的操作日志
            {
                path: '/actionLog',
                name: 'actionLog',
                component: () => import('@/page/ordinary/ActionLog'),
                meta: {title: '普通用户-我的操作日志'},
            },
            // 用户评分页面
            {
                path: '/quesnaireEdit',
                name: 'quesnaireEdit',
                component: () => import('@/page/ordinary/QuesnaireEdit'),
                meta: {title: '普通用户-用户评分页面'},
            }
        ]
    },
    // 管理员端
    {
        path: '/admin',
        name: 'admin',
        component: AdminLayout,
        redirect: '/personalScoreAd',
        meta: { requiresAuth: true, role: ['1', '2', '3'] },
        children: [
            // 个人得分
            {
                path: '/personalScoreAd',
                name: 'personalScoreAd',
                component: () => import('@/page/admin/PersonalScoreAd'),
                meta: {title: '管理员-个人得分'},
            },
            // 我的待办
            {
                path: '/todoListAd',
                name: 'todoListAd',
                component: () => import('@/page/admin/TodoListAd'),
                meta: {title: '管理员-我的待办'},
            },
            // 修改个人信息
            {
                path: '/personalInfoAd',
                name: 'personalInfoAd',
                component: () => import('@/page/admin/PersonalInfoAd'),
                meta: {title: '管理员-修改个人信息'},
            },
            // 我的操作日志
            {
                path: '/actionLogAd',
                name: 'actionLogAd',
                component: () => import('@/page/admin/ActionLogAd'),
                meta: {title: '管理员-我的操作日志'},
            },
            // 填写评分问卷页面
            {
                path: '/quesnaireEditAd',
                name: 'quesnaireEditAd',
                component: () => import('@/page/admin/QuesnaireEditAd'),
                meta: {title: '管理员-用户评分页面'},
            },
            // 评分看板
            {
                path: '/rateBoard',
                name: 'rateBoard',
                component: () => import('@/page/admin/RateBoard'),
                meta: {title: '管理员-评分看板'},
            },
            // 得分看板
            {
                path: '/scoreBoard',
                name: 'scoreBoard',
                component: () => import('@/page/admin/ScoreBoard'),
                meta: {title: '管理员-得分看板'},
            },
            // 设置评分节点
            {
                path: '/scoringTimeSet',
                name: 'scoringTimeSet',
                component: () => import('@/page/admin/ScoringTimeSet'),
                meta: {title: '管理员-设置评分节点'},
            },
            // 维护评估矩阵
            {
                path: '/scoringMatrix',
                name: 'scoringMatrix',
                component: () => import('@/page/admin/ScoringMatrix'),
                meta: {title: '管理员-维护评估矩阵'},
            },
            // 维护卷首提醒
            {
                path: '/headnoteEdit',
                name: 'headnoteEdit',
                component: () => import('@/page/admin/HeadnoteEdit'),
                meta: {title: '管理员-维护卷首提醒'},
            },
            // 用户管理
            {
                path: '/userManage',
                name: 'userManage',
                component: () => import('@/page/admin/UserManage'),
                meta: {title: '管理员-用户管理'},
            }
        ]
    },
]

// 创建路由实例
const router = createRouter({
    history: createWebHistory(process.env.BASE_URL),
    routes
  })

// 路由守卫 - 用来拦截访问权限
router.beforeEach((to, from, next) => {
    // 1. 从 localStorage 获取当前用户角色，并保存当前路径
    const userRole = localStorage.getItem('userRole');
    const requiredRole = to.meta.role;
  
    // 2. 判断目标路由是否需要认证，且用户没有登录
    if (to.meta.requiresAuth && !userRole) {
      message.warning('您还未登录，请先登录！');
      return next({ path: '/' });  // 没有登录，跳转到登录页
    }

    // 3. 针对 /userManage 页面，允许角色为 3 或 2 的用户访问
    if (to.path === '/userManage' && !(userRole === '3' || userRole === '2')) {
        message.warning('您没有访问该页面的权限哦~');
        return next({ path: '/' });  // 如果不是角色 3 或 2，跳转到主页
    }
  
    // 4. 检查普通用户与管理员之间的权限守卫
    if (requiredRole && !requiredRole.includes(userRole)) {
        message.warning('您没有访问该页面的权限哦~');
        // 根据用户角色进行跳转
        if (userRole === '0') {
            return next({ path: '/ordinary' });  // 普通用户跳转到普通用户端
        } else if (userRole === '1' || userRole === '2' || userRole === '3') {
            return next({ path: '/admin' });  // 管理员跳转到管理员端
        }
    }
  
    // 5. 如果通过了检查，放行路由
    next();
  });
  
export default router

