<template>
    <!-- 使用a-layout布局组件 -->
    <a-layout>
        <!-- 顶部导航区域，设置为窗口高度的10% -->
        <a-layout-header class="header">
            <!-- Logo区域 -->
            <div class="sysLogo">
                <img src="@/assets/sysLogo.png" alt="logo" class="logo-img"/>
                <img src="@/assets/slogan.png" alt="slogan" class="slogan">
            </div>
            <!-- 头像名字区域 -->
            <div class="userName">
                <a-avatar size="large" :style="{ backgroundColor: '#1E90FF', verticalAlign: 'middle', marginRight: '10px' }" :gap="gap">Ad</a-avatar>
                <a-dropdown placement='bottomRight'>
                    <span>您好！{{ username }} <span style="color: rgba(70, 130, 180, 1); font-weight: bold;">{{ level }}管理员</span></span>
                    <template #overlay>
                        <a-menu>
                            <a-menu-item @click="logout">
                                退出登录
                            </a-menu-item>
                        </a-menu>
                    </template>
                </a-dropdown>
            </div>
        </a-layout-header>
        <!-- 主布局，包含侧边栏和内容区，主布局高度设置为90vh -->
        <a-layout :style="{ height: '90vh', padding: '15px 0 0 0' }">
            <!-- 侧边栏布局，宽度为200px，背景色为白色 -->
            <!-- 侧边栏菜单，selectedKeys用于控制选中项，openKeys控制哪些子菜单展开 -->
            <a-layout-sider width="200" style="background: #fff;">
                <a-menu id="menu" v-model:openKeys="openKeys" v-model:selectedKeys="selectedKeys" style="width: 200px"
                    mode="inline" :items="items" @click="handleMenuClick"></a-menu>
            </a-layout-sider>

            <!-- 主内容布局，内容区添加了圆角和阴影 -->
            <a-layout style="padding: 10px 20px 20px 20px">
                <a-layout-content :style="{
                    width: '100%',
                    height: '100%',
                    background: '#fff',
                    padding: '10px',
                    margin: 0,
                    // minHeight: '280px',
                    borderRadius: '10px',
                    overflow: 'auto',
                    boxShadow: '0px 4px 12px rgba(0, 0, 0, 0.1)'
                }">
                    <router-view></router-view>
                </a-layout-content>
            </a-layout>
        </a-layout>
    </a-layout>
</template>

<script setup>
import {
    EyeOutlined,
    ApartmentOutlined,
    ClockCircleOutlined,
    FileTextOutlined,
    UserOutlined,
    CommentOutlined,
    CheckCircleOutlined,
    TeamOutlined,
    SettingOutlined
} from '@ant-design/icons-vue';
import { reactive, ref, watch, h, onMounted } from 'vue'; // 从Vue中导入ref用于定义响应式变量
import { useRouter, useRoute } from 'vue-router';
import store from '@/store'; // 引入 store
import { message } from 'ant-design-vue';
import { logoutApi } from '@/api/login'

onMounted(() => {
    // 页面加载时，根据当前路由路径设置 selectedKeys
    selectedKeys.value = [route.path];
})

const router = useRouter();
const route = useRoute(); // 用于获取当前路由信息

const selectedKeys = ref();
const openKeys = ref(['userFunctions', 'adminFunctions']);
const username = ref(localStorage.getItem('username'));
const role = ref(localStorage.getItem('userRole'))

const level = ref('')

if(role.value === '1'){
    level.value = '一级'
}else if(role.value === '2'){
    level.value = '二级'
}else if(role.value === '3'){
    level.value = '超级'
}else {
    level.value = ''
}

function getItem(label, key, icon, children, type) {
    return {
        key,
        icon,
        children,
        label,
        type,
    };
}
let items = reactive()
if (role.value === '3' || role.value === '2') {
    items = [
        getItem('普通用户功能', 'userFunctions', () => h(UserOutlined), [
            getItem('个人得分', '/personalScoreAd', () => h(CheckCircleOutlined)),
            getItem('我的待办', '/todoListAd', () => h(CommentOutlined)),
            getItem('修改个人信息', '/personalInfoAd', () => h(UserOutlined)),
            getItem('我的操作日志', '/actionLogAd', () => h(TeamOutlined)),
        ]),
        getItem('管理员功能', 'adminFunctions', () => h(SettingOutlined), [
            getItem('评分看板', '/rateBoard', () => h(EyeOutlined)),
            getItem('得分看板', '/scoreBoard', () => h(EyeOutlined)),
            getItem('设置评分节点', '/scoringTimeSet', () => h(ClockCircleOutlined)),
            getItem('维护评分矩阵', '/scoringMatrix', () => h(ApartmentOutlined)),
            getItem('维护卷首提醒', '/headnoteEdit', () => h(FileTextOutlined)),
            getItem('用户管理', '/userManage', () => h(UserOutlined)),
        ]),
    ]
} else {
    items = [
        getItem('普通用户功能', 'userFunctions', () => h(UserOutlined), [
            getItem('个人得分', '/personalScoreAd', () => h(CheckCircleOutlined)),
            getItem('我的待办', '/todoListAd', () => h(CommentOutlined)),
            getItem('修改个人信息', '/personalInfoAd', () => h(UserOutlined)),
            getItem('我的操作日志', '/actionLogAd', () => h(TeamOutlined)),
        ]),
        getItem('管理员功能', 'adminFunctions', () => h(SettingOutlined), [
            getItem('评分看板', '/rateBoard', () => h(EyeOutlined)),
            getItem('得分看板', '/scoreBoard', () => h(EyeOutlined)),
            getItem('设置评分节点', '/scoringTimeSet', () => h(ClockCircleOutlined)),
            getItem('维护评分矩阵', '/scoringMatrix', () => h(ApartmentOutlined)),
            getItem('维护卷首提醒', '/headnoteEdit', () => h(FileTextOutlined)),
        ]),
    ]
}
// 点击菜单项，实现页面跳转
const handleMenuClick = (event) => {
    router.push({path: event.key})
};

// 退出登录
const logout = () => {
    logoutApi().then(res => {
        if(res.data.code === 200){
            router.push({ path: '/' })
            message.success('您已退出登录！');
        }
    }).catch(err => {
        if(err.data.code === 403){
            router.push({ path: '/' })
            message.error('登录信息已失效，请重新登录！');
        }else {
            console.log('退出登录失败！', err)
        }
    })
}

</script>

